import React, { useEffect, useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import CKEditor from "ckeditor4-react";
import AdminNav from "../AdminNav";
import SideBar from "../SideBar";
import "./EditEvent.css";
import axios from "../../axios";
import striptags from "striptags";
import { useParams } from "react-router";

function EditEvent() {
  const [startDate, setStartDate] = useState(new Date());
  const [eventData, setEventData] = useState([]);
  const [theme, setTheme] = useState("");
  const [host, setHost] = useState("");
  // const [photo, setPhoto] = useState("");
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(false);

  const { id } = useParams();

  useEffect(() => {
    setTheme(eventData.theme);
    setHost(eventData.host);
    setDescription(eventData.description);
  }, [eventData]);

  // Get description to Edits info
  useEffect(() => {
    async function fetchData() {
      const { data } = await axios.get("/events/" + id);
      setEventData(data.data);
      return data;
    }
    fetchData();
  }, [id]);

  // const changeFile = (e) => {
  //   setPhoto(e.target.files[0]);
  // };
  

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    let formData = {
      theme,
      host,
      startDate,
      description: striptags(description, [
        "p",
        "h1",
        "h2",
        "h3",
        "h4",
        "h5",
        "h6",
        "a",
        "b",
        "strong",
        "ul",
        "li",
      ]),
    };

    if (theme === "" || theme.length < 6) {
      console.log("Please theme must above 6 charcters");
      return;
    }
    if (host === "" || host.length < 3) {
      console.log("Please category must above 6 charcters");
      return;
    }
    if (description === "" || description.length < 10) {
      console.log("Please enter a valid description");
      return;
    }

    try {
      const postData = await axios.patch("/events/" + id, formData);
      if (postData) {
        window.location = "/admin/events";
        setLoading(false);
      }
    } catch (err) {
      console.log(err.message);
      setLoading(false);
    }
  };

  return (
    <div className="editEvents">
      <SideBar title="Edit Event" />
      <div className="editEvents__content">
        <AdminNav />
        <div className="editEvents__main">
          <div className="formContainer">
            <div className="card">
              <h3 className="title-md">Edit Event</h3>
              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="inputCtn">
                  <label htmlFor="theme">Theme</label>
                  <input
                    type="text"
                    name="theme"
                    className="inputField"
                    placeholder="theme"
                    value={theme ? theme : ''}
                    onChange={(e) => setTheme(e.target.value)}
                  />
                </div>
                <div className="inputCtn">
                  <label htmlFor="host">Host</label>
                  <input
                    type="text"
                    name="host"
                    className="inputField"
                    placeholder="Host"
                    value={host ? host : ''}
                    onChange={(e) => setHost(e.target.value)}
                  />
                </div>
                <div className="inputCtn">
                  <label htmlFor="date">Date</label>
                  <DatePicker
                    dateFormat="dd/MM/yyy"
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                  />
                </div>
                {/* <div className="inputCtn">
                  <label htmlFor="photo">Event Photo</label>
                  <input
                    type="file"
                    name="photo"
                    className="inputField"
                    placeholder="Photo "
                    onChange={changeFile}
                  />
                </div> */}
                <div className="inputCtn bg-teal ">
                  <label htmlFor="descripton">Description</label>
                  <CKEditor
                    data={description}
                    onChange={(e) => setDescription(e.editor.getData())}
                    name="descripton"
                  />
                </div>
                <div className="inputCtn">
                  {loading ? (
                    <button disabled onClick={handleSubmit} className="btn">
                      Publish
                    </button>
                  ) : (
                    <button onClick={handleSubmit} className="btn">
                      Publish
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EditEvent;
