import React, { useState } from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import CKEditor from "ckeditor4-react";
import AdminNav from "../AdminNav";
import SideBar from "../SideBar";
import striptags from "striptags";
import "./AddEvent.css";
import axios from "../../axios";

function AddEvent() {
  const [startDate, setStartDate] = useState(new Date());
  const [theme, setTheme] = useState("");
  const [host, setHost] = useState("");
  const [description, setDescription] = useState("");
  const [loading, setLoading] = useState(false);
  const [photo, setPhoto] = useState("");

  const changeFile = (e) => {
    setPhoto(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    let formData = new FormData();
    formData.append("theme", theme);
    formData.append("host", host);
    formData.append(
      "description",
      striptags(description, [
        "p",
        "h1",
        "h2",
        "h3",
        "h4",
        "h5",
        "h6",
        "a",
        "b",
        "strong",
        "ul",
        "li",
      ])
    );
    formData.append("startDate", startDate);
    formData.append("photo", photo);

    if (theme === "" || theme.length < 6) {
      console.log("Please theme must above 6 charcters");
      return;
    }
    if (host === "" || host.length < 3) {
      console.log("Please category must above 6 charcters");
      return;
    }
    if (description === "" || description.length < 10) {
      console.log("Please enter a valid description");
      return;
    }

    try {
      const postData = await axios.post("/events", formData, {
        headers: {
          "content-type": "multipart/form-data",
        },
      });
      if (postData) {
        window.location = "/admin/events";
        setLoading(false);
      }
    } catch (err) {
      console.log(err.message);
      setLoading(false);
    }
  };

  return (
    <div className="addEvents">
      <SideBar title="Add Event" />
      <div className="addEvents__content">
        <AdminNav />
        <div className="addEvents__main">
          <div className="formContainer">
            <div className="card">
              <h3 className="title-md">Add Event</h3>
              <form onSubmit={handleSubmit} encType="multipart/form-data">
                <div className="inputCtn">
                  <label htmlFor="title">Title</label>
                  <input
                    type="text"
                    name="title"
                    className="inputField"
                    placeholder="title"
                    onChange={(e) => setTheme(e.target.value)}
                  />
                </div>
                <div className="inputCtn">
                  <label htmlFor="host">Host</label>
                  <input
                    type="text"
                    name="host"
                    className="inputField"
                    placeholder="Host"
                    onChange={(e) => setHost(e.target.value)}
                  />
                </div>
                <div className="inputCtn">
                  <label htmlFor="date">Date</label>

                  <DatePicker
                    dateFormat="dd/MM/yyy"
                    selected={startDate}
                    onChange={(date) => setStartDate(date)}
                  />
                </div>
                <div className="inputCtn">
                  <label htmlFor="eventPhoto">Event Photo</label>
                  <input
                    type="file"
                    id="eventPhoto"
                    name="eventPhoto"
                    className="inputField"
                    placeholder="Photo "
                    onChange={changeFile}
                  />
                </div>
                <div className="inputCtn bg-teal ">
                  <label htmlFor="descripton">Descripton</label>
                  <CKEditor
                    data={description}
                    onChange={(e) => setDescription(e.editor.getData())}
                    name="descripton"
                    style={{
                      marginTop: "10px",
                    }}
                  />
                </div>
                <div className="inputCtn">
                  {loading ? (
                    <button disabled onClick={handleSubmit} className="btn">
                      Publish
                    </button>
                  ) : (
                    <button onClick={handleSubmit} className="btn">
                      Publish
                    </button>
                  )}
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddEvent;
